import type { EventEmitter } from '@stencil/core';
import { Component, Element, Host, Prop, State, Watch, h, Event } from '@stencil/core';

@Component({
	tag: 'o-radio-group',
	styleUrl: 'o-radio-group.scss',
	shadow: true,
	formAssociated: true
})
export class ORadioGroup {
	private defaultValue: string;
	private valueHasChanged: boolean = false;

	@Element() host!: HTMLORadioGroupElement;

	/** @prop {string} label - The label text for the radio group. */
	@Prop() label: string;

	/** @prop {string} name - The name attribute used to bind the child radios together. */
	@Prop() name: string;

	/** @prop {string} value - The value attribute for the radio group. */
	@Prop({ mutable: true }) value: string;

	/** @prop {string} helpText - The help text to display below the radio group. */
	@Prop() helpText: string = '';

	/** @prop {string} errorText - The error message to display when validation fails. */
	@Prop() errorText: string = '';

	/** @prop {boolean} required - Whether selection from the radio group is required. Default is false. */
	@Prop() required: boolean = false;

	/** @state {boolean} hasError - Tracks whether the radio group has an error. */
	@State() hasError: boolean = false;

	/** Emitted when the radio group value changes */
	@Event() oChange: EventEmitter<{ value: string }>;

	private internals: ElementInternals | null = null;

	constructor() {
		// Validation to allow unit tests to run without ElementInternals support
		try {
			if (typeof this.host.attachInternals === 'function') {
				this.internals = this.host.attachInternals();
			}
		} catch (e) {
			/* istanbul ignore next */
			console.warn('ElementInternals not supported', e.message);
		}
	}

	connectedCallback() {
		this.validateInput(this.value);
	}

	componentDidLoad() {
		this.defaultValue = this.value;
	}

	/* istanbul ignore next */
	@Watch('value')
	watchValue(newValue: string) {
		if (!this.valueHasChanged && this.defaultValue === newValue) {
			return;
		}
		this.validateInput(newValue);
		this.updateFormValue(newValue);
		this.updateRadioGroupProps();
		this.valueHasChanged = true;
	}

	@Watch('errorText')
	errorTextChanged() {
		this.validateInput(this.value);
		this.updateValidityState();
	}

	private updateRadioGroupProps() {
		const radios = Array.from(this.host.querySelectorAll('o-radio'));
		radios.forEach((radio) => {
			radio.name = this.name;
			radio.checked = radio.value === this.value;
		});
	}

	/* istanbul ignore next */
	private handleClick = (event: MouseEvent) => {
		const target = (event.target as HTMLElement).closest('o-radio') as HTMLORadioElement;
		const radios = Array.from(this.host.querySelectorAll('o-radio'));
		if (!target || target.disabled) {
			return;
		}
		if (!this.valueHasChanged && this.defaultValue === target.value) {
			return;
		}
		this.value = target.value;
		radios.forEach((radio) => (radio.checked = radio === target));
		this.validateInput(this.value);
		this.updateFormValue(this.value);
		this.oChange.emit({ value: this.value });
		this.valueHasChanged = true;
	};

	/** Validates the radio group state */
	private validateInput(selectedValue: string) {
		this.hasError = (!selectedValue || selectedValue.trim() === '') && this.errorText.trim() !== '';
		this.updateValidityState();
	}

	/** Update validity state for form association */
	private updateValidityState() {
		if (!this.internals) return;
		// Allow unit tests to run without ElementInternals support
		try {
			/* istanbul ignore if */
			if (this.hasError) {
				this.internals.setValidity?.(
					{ customError: true },
					this.errorText,
					this.host.shadowRoot.querySelector('fieldset')
				);
			} else {
				this.internals.setValidity?.({});
			}
		} catch (e) {
			/* istanbul ignore next */
			console.warn('Validity state update not supported', e.message);
		}
	}

	/** Update form value for form association */
	private updateFormValue(value: string) {
		/* istanbul ignore if */
		if (this.internals?.setFormValue) {
			try {
				this.internals.setFormValue(value || null);
			} catch (e) {
				console.warn('Form value update not supported', e.message);
			}
		}
	}

	render() {
		return (
			<Host>
				<fieldset onClick={this.handleClick}>
					<legend class="pb-2">
						{this.label && (
							<o-label
								label={this.label}
								variant={this.hasError ? 'error' : undefined}
								required={this.required}
							></o-label>
						)}
					</legend>
					<slot onSlotchange={() => this.updateRadioGroupProps()}></slot>
				</fieldset>
				<div class="body-default">
					{this.helpText && <div class="mt-1 text-content-default">{this.helpText}</div>}
					{this.hasError && (
						<div class="mt-1">
							<span class="text-content-status-error">{this.errorText}</span>
						</div>
					)}
				</div>
			</Host>
		);
	}
}
